import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import React, { useState } from "react";
import { Button } from "react-bootstrap";
import { useLocalDB } from "../database/LocalDB";
import { ReturnForm } from "../component/ReturnForm";
import { aggregateSum } from "../util/List";
import "../App.css";

const { SearchBar, ClearSearchButton } = Search;

export const Borrower = () => {
  const { getAllBorrowers } = useLocalDB();

  const [returnTool, setReturnTool] = useState({
    header: "",
    show: false,
    item: "",
    borrowers: {},
    borrowerName: "",
    transactionDate: 0,
  });

  const columns = [
    {
      dataField: "name",
      text: "Borrower Name",
      searchable: true,
      sort: true,
    },
    {
      dataField: "items",
      text: "Borrowed Items",
      formatter: (cell, row) => (
        <div>
          {aggregateSum({
            list: cell,
            byFieldName: "item",
            fieldNameToSum: "quantity",
          }).map((el, i) => (
            <div key={i}>{`${el.sum} ${el.byFieldName}`}</div>
          ))}
        </div>
      ),
      searchable: false,
    },
    {
      dataField: "action",
      text: "Action",
      formatter: (cell, row) => {
        return (
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
            }}
          >
            <Button
              variant="outline-primary"
              size="sm"
              onClick={() => {
                const borrowers = {
                  [row.name]: row.items,
                };
                const defaultData = row.items[0];
                setReturnTool({
                  header: `Return tool for ${row.name}`,
                  show: true,
                  item: defaultData.item,
                  borrowers,
                  borrowerName: row.name,
                  transactionDate: defaultData.transactionDate,
                });
              }}
            >
              Return Tool
            </Button>
          </div>
        );
      },
      searchable: false,
      headerStyle: (colum, colIndex) => {
        return { width: "20%" };
      },
    },
  ];

  return (
    <>
      <ReturnForm
        state={returnTool}
        handleClose={() =>
          setReturnTool({
            header: "",
            show: false,
            item: "",
            borrowers: {},
            borrowerName: "",
            transactionDate: 0,
          })
        }
      />
      <ToolkitProvider
        bootstrap4
        keyField="name"
        data={getAllBorrowers()}
        columns={columns}
        search={{ defaultSearch: "" }}
      >
        {(props) => (
          <div className="w-75 container-center">
            <br className="mt-30" />
            <SearchBar
              {...props.searchProps}
              srText="Search by Borrower Name"
              placeholder="Borrower Name"
            />
            <ClearSearchButton {...props.searchProps} />
            <br className="mt-30" />
            <br className="mt-30" />
            <BootstrapTable
              {...props.baseProps}
              noDataIndication="No Borrowers"
              hover
              condensed
              striped
              bordered={ false }
            />
          </div>
        )}
      </ToolkitProvider>
    </>
  );
};
