import React, { useState } from "react";
import { useEffect } from "react";
import { Button, Modal } from "react-bootstrap";
import Select from "react-select";
import { useLocalDB } from "../database/LocalDB";
import { optionStyles } from "../util/UI";

export const ReturnForm = ({ state, handleClose }) => {
  const [data, setData] = useState({
    selectedName: state.borrowerName,
    selectedTransactionDate: state.transactionDate,
  });

  useEffect(() => {
    setData({
      selectedName: state.borrowerName,
      selectedTransactionDate: state.transactionDate,
    });
  }, [state]);

  const { getTransaction, returnTool } = useLocalDB();

  const handleSubmit = () => {
    const returnToolTransaction = getTransaction({
      transactionDate: data.selectedTransactionDate,
    });

    if (returnToolTransaction === undefined) {
      console.error(
        `Transaction date (${data.selectedTransactionDate}) not found.`
      );
      return;
    }

    returnTool(returnToolTransaction);
    handleClose();
  };

  const handleNameChange = (e) => {
    const selectedName = e.value;
    setData({
      selectedName,
      selectedTransactionDate: (state.borrowers[selectedName] || [])[0]?.transactionDate,
    });
    console.log(`handleNameChange ${selectedName}`);
  };

  const handleDetailsChange = (e) => {
    const selectedTransactionDate = Number(e.value);
    setData({
      selectedName: data.selectedName,
      selectedTransactionDate,
    });
    console.log(`handleDetailsChange ${selectedTransactionDate}`);
  };

  return (
    <Modal show={state.show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>{state.header}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="w-100 mb-3">
          <Select
            styles={optionStyles}
            value={(() => {
              return {
                value: data.selectedName || "Select a borrower",
                label: data.selectedName || "Select a borrower",
              };
            })()}
            onChange={handleNameChange}
            options={Object.keys(state.borrowers).map((fullName) => ({
              value: fullName,
              label: fullName,
            }))}
          />
        </div>
        <div className="w-100 mb-3">
          <Select
            styles={optionStyles}
            value={(() => {
              const t = (state.borrowers[data.selectedName] || []).find(({transactionDate}) => `${transactionDate}` === `${data.selectedTransactionDate}`);
              return {
                value: t ? t.transactionDate : "Select an item",
                label: t
                  ? `${t.quantity} ${t.item} with return date of ${t.returnDate} and transaction date ${new Date(t.transactionDate)}`
                  : "Select an item",
              };
            })()}
            onChange={handleDetailsChange}
            options={(state.borrowers[data.selectedName] || []).map((t) => ({
              value: t.transactionDate,
              label: `${t.quantity} ${t.item} with return date of ${t.returnDate} and transaction date of ${new Date(t.transactionDate)}`,
            }))}
          />
        </div>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Cancel
        </Button>
        <Button variant="primary" onClick={handleSubmit}>
          Return Tool
        </Button>
      </Modal.Footer>
    </Modal>
  );
};
