import dayjs from "dayjs";
import { Formik } from "formik";
import React from "react";
import { Button, FormControl, InputGroup, Modal } from "react-bootstrap";
import { useLocalDB } from "../database/LocalDB";
import { isValidName, padRight15 } from "../util/String";

export const BorrowerForm = ({ state, handleClose }) => {
  const { borrowTool } = useLocalDB();
  return (
    <Formik
      initialValues={{
        firstName: "",
        lastName: "",
        quantity: 1,
        returnDate: "",
      }}
      validateOnChange={false}
      validateOnBlur={false}
      validate={(values) => {
        return {
          ...(values.firstName.length === 0
            ? { firstName: "First name is required." }
            : {}),
          ...(values.firstName.length > 0 && !isValidName(values.firstName)
            ? {
                firstName:
                  "First name should not have space, numeric or special character",
              }
            : {}),
          ...(values.lastName.length === 0
            ? { lastName: "Last name is required." }
            : {}),
          ...(values.lastName.length > 0 && !isValidName(values.lastName)
            ? {
                lastName:
                  "Last name should not have space, numeric or special character",
              }
            : {}),
          ...(Number(values.quantity) <= 0
            ? { quantity: "Quantity is required" }
            : {}),
          ...(values.returnDate.length === 0
            ? { returnDate: "Return date is required" }
            : {}),
          ...(values.returnDate.length > 0 && dayjs(values.returnDate, 'MM/DD/YYYY').isBefore(dayjs(), 'day')
            ? { returnDate: "Return date cannot be from the past" }
            : {}),
        };
      }}
      onSubmit={(values, { resetForm }) => {
        borrowTool({
          ...values,
          quantity: Number(values.quantity),
          item: state.item,
        });

        resetForm();
        handleClose();
      }}
    >
      {({ handleSubmit, handleChange, resetForm, values, errors }) => {
        const _handleClose = () => {
          resetForm();
          handleClose();
        };

        return (
          <Modal show={state.show} onHide={_handleClose}>
            <Modal.Header closeButton>
              <Modal.Title>{`Borrow ${state.item} tool`}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <InputGroup className="mb-3">
                <InputGroup.Text>{padRight15("First Name")}</InputGroup.Text>
                <FormControl
                  aria-label="First name"
                  type="text"
                  name="firstName"
                  value={values.firstName}
                  onChange={handleChange}
                  isInvalid={!!errors.firstName}
                />
                <FormControl.Feedback type="invalid">
                  {errors.firstName}
                </FormControl.Feedback>
              </InputGroup>
              <InputGroup className="mb-3">
                <InputGroup.Text>{padRight15("Last Name")}</InputGroup.Text>
                <FormControl
                  aria-label="Last name"
                  type="text"
                  name="lastName"
                  value={values.lastName}
                  onChange={handleChange}
                  isInvalid={!!errors.lastName}
                />
                <FormControl.Feedback type="invalid">
                  {errors.lastName}
                </FormControl.Feedback>
              </InputGroup>
              <InputGroup className="mb-3">
                <InputGroup.Text>Quantity</InputGroup.Text>
                <FormControl
                  as="select"
                  name="quantity"
                  value={values.quantity}
                  onChange={handleChange}
                  isInvalid={!!errors.quantity}
                >
                  {[...Array(state.available).keys()]
                    .map((e) => e + 1)
                    .map((el) => (
                      <option key={el} value={el}>
                        {el}
                      </option>
                    ))}
                </FormControl>
                <FormControl.Feedback type="invalid">
                  {errors.quantity}
                </FormControl.Feedback>
              </InputGroup>
              <InputGroup className="mb-3">
                <InputGroup.Text>Return Date</InputGroup.Text>
                <FormControl
                  aria-label="Return Date"
                  type="date"
                  name="returnDate"
                  dateFormat="YYYY-MM-DD"
                  value={values.returnDate}
                  onChange={handleChange}
                  isInvalid={!!errors.returnDate}
                />
                <FormControl.Feedback type="invalid">
                  {errors.returnDate}
                </FormControl.Feedback>
              </InputGroup>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={_handleClose}>
                Cancel
              </Button>
              <Button variant="primary" onClick={handleSubmit}>
                Borrow Tool
              </Button>
            </Modal.Footer>
          </Modal>
        );
      }}
    </Formik>
  );
};
