import React from "react";
import { Navbar, Container, Nav } from "react-bootstrap";
import { capitalizeFirstLetter } from "../util/String";

export const AVHeader = (props) => (
  <Navbar collapseOnSelect expand="lg" bg="primary" variant="dark">
    <Container>
      <Navbar.Brand>Avionics Tools</Navbar.Brand>
      {props.username ? AVHeaderUser(props) : <></>}
    </Container>
  </Navbar>
);

const AVHeaderUser = ({ username, onLogout, onDashboard, onInventory, onBorrowers }) => (
  <>
    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
    <Navbar.Collapse id="responsive-navbar-nav">
      <Nav className="me-auto" defaultActiveKey="#dashboard">
        <Nav.Link href="#dashboard" onClick={onDashboard}>Dashboard</Nav.Link>
        <Nav.Link href="#inventory" onClick={onInventory}>Inventory</Nav.Link>
        <Nav.Link href="#borrower" onClick={onBorrowers}>Borrower</Nav.Link>
      </Nav>
    </Navbar.Collapse>
    <Navbar.Collapse className="justify-content-end">
      <Navbar.Text>
        <small>Signed in as: {capitalizeFirstLetter(username)}</small>
      </Navbar.Text>
      <Nav>
        <Nav.Link eventKey={2} onClick={onLogout}>
          Logout
        </Nav.Link>
      </Nav>
    </Navbar.Collapse>
  </>
);
