export const capitalizeFirstLetter = ([head, ...tail]) => `${head.toUpperCase()}${tail.join("").toLowerCase()}`;

export const padRight = ({ source, padChar = " ", length }) => {
    const maxLength = Math.max(source.length, length);
    const spaces = Array.from(Array(maxLength-source.length).keys()).map(e => padChar).join("")
    return source.concat(spaces);
}

export const padRight15 = (source) => padRight({ source, length: 15 });

export const isValidName = (name) => /^[a-zA-Z ]+$/.test(name);