import React, { useState } from "react";
import { AVHeader } from "../component/AVHeader";
import { Borrower } from "./Borrower";
import { Dashboard } from "./Dashboard";
import { Inventory } from "./Inventory";

export const SubScreenType = {
  DASHBOARD: "DASHBOARD",
  INVENTORY: "INVENTORY",
  BORROWER: "BORROWER",
};

const Content = ({ subScreenType }) => {
  switch(subScreenType){
    case SubScreenType.DASHBOARD:
      return <Dashboard />;
    case SubScreenType.INVENTORY:
      return <Inventory />;
    case SubScreenType.BORROWER:
      return <Borrower />;
    default:
      throw new Error(`subScreenType ${subScreenType} not found`);
  }
}

export const Home = ({ username, onLogout, defaultSubScreenType = SubScreenType.DASHBOARD }) => {
  const [subScreenType, setSubScreenType] = useState(defaultSubScreenType);

  return (
    <>
      <AVHeader
        username={username}
        onLogout={onLogout}
        onDashboard={() => setSubScreenType(SubScreenType.DASHBOARD)}
        onInventory={() => setSubScreenType(SubScreenType.INVENTORY)}
        onBorrowers={() => setSubScreenType(SubScreenType.BORROWER)}
      />
      <Content subScreenType={subScreenType} />
    </>
  );
};
