export const TransactionAction = {
  BORROW_TOOL: "BORROW_TOOL",
  RETURN_TOOL: "RETURN_TOOL",
  BORROW_AND_RETURN_TOOL: "BORROW_AND_RETURN_TOOL",
  OUT_OF_STOCK: "OUT_OF_STOCK",
};

export const getPossibleTransactionAction = ({ total, available }) => {
  if (total === 0 && available === 0) {
    return TransactionAction.OUT_OF_STOCK;
  } else if (total > 0 && available === 0) {
    return TransactionAction.RETURN_TOOL;
  } else if (total > 0 && total === available) {
    return TransactionAction.BORROW_TOOL;
  } else {
    return TransactionAction.BORROW_AND_RETURN_TOOL;
  }
};

export const optionStyles = {
  option: (provided, state) => ({
    ...provided,
    whiteSpace: 'pre-wrap',
  }),
}
