import React, { useEffect } from "react";
import { Home } from "./container/Home";
import { Login } from "./container/Login";
import { LocalDBProvider, useLocalDB } from "./database/LocalDB";
import "./App.css";

const App = (props) => {
  useEffect(() => {
    document.title = "Avionics Tools";
  }, []);

  return (
    <LocalDBProvider>
      <Main />
    </LocalDBProvider>
  );
};

const Main = () => {
  const { getCurrentUser, logout } = useLocalDB();
  const username = getCurrentUser();
  if (username) {
    return <Home username={username} onLogout={logout} />;
  } else {
    return <Login />;
  }
};

export default App;
