import { createContext, useContext, useReducer } from "react";
import { groupBy } from "../util/List";
import { getPossibleTransactionAction } from "../util/UI";

const users = require("./users.json");
const tools = require("./tools.json");

const LocalDBActionType = {
  LOGIN: "LOGIN",
  LOGOUT: "LOGOUT",
  ADD_TRANSACTION: "ADD_TRANSACTION",
};

const TransactionType = (() => {
  return {
    BORROWED: "BORROWED",
    RETURNED: (transactionDate) => `RETURNED ${transactionDate}`,
    getReturnedTransactionDate: (returned) =>
      Number(returned.split(" ")[1] || 0),
  };
})();

const defaultInitialState = {
  userLoggedIn: undefined,
  transactions: [],
};

const Persistence = (() => {
  const key = "avionics_tools_state";

  return {
    set: (state) => localStorage.setItem(key, JSON.stringify(state)),
    getState: () => JSON.parse(localStorage.getItem(key)),
  };
})();

const Reducer = (state, action) => {
  switch (action.type) {
    case LocalDBActionType.LOGIN:
      const loginNewState = {
        ...state,
        userLoggedIn: action.payload,
      };
      Persistence.set(loginNewState);
      return loginNewState;
    case LocalDBActionType.LOGOUT:
      const logoutNewState = {
        ...state,
        userLoggedIn: undefined,
      };
      Persistence.set(logoutNewState);
      return logoutNewState;
    case LocalDBActionType.ADD_TRANSACTION:
      const newState = {
        ...state,
        transactions: state.transactions.concat(action.payload),
      };
      Persistence.set(newState);
      return newState;
  }
};

const LocalDBContext = createContext(defaultInitialState);

export const useLocalDB = () => {
  const localDBContext = useContext(LocalDBContext);
  if (localDBContext === undefined) {
    throw new Error("useLocalDB must be used within a LocalDBProvider");
  } else {
    return localDBContext;
  }
};

const sumOfBorrowedTool = (transactions) =>
  transactions
    .filter((item) => item.type === TransactionType.BORROWED)
    .reduce((result, { quantity }) => result + Number(quantity), 0);

const sumOfReturnedTool = (transactions) =>
  transactions
    .filter((item) => item.type !== TransactionType.BORROWED)
    .reduce((result, { quantity }) => result + Number(quantity), 0);

const getBorrowersGroup = (transactions) =>
  groupBy({
    list: transactions,
    fieldNames: ["firstName", "lastName"],
  });

export const LocalDBProvider = ({ children }) => {
  const [state, dispatch] = useReducer(Reducer, {
    ...defaultInitialState,
    ...Persistence.getState(),
  });

  const getCurrentUser = () => state.userLoggedIn;

  const getComputedTransactionData = () => ({
    approver: getCurrentUser(),
    transactionDate: new Date().getTime(),
  });

  const doesUserExists = ({ username, password }) =>
    users.some(
      ({ uname, pwd }) =>
        uname.toLowerCase() === username.toLowerCase() &&
        pwd.toLowerCase() === password.toLowerCase()
    );

  const login = (username) => {
    // @ts-ignore
    dispatch({
      type: LocalDBActionType.LOGIN,
      payload: username,
    });
  };

  const logout = () => {
    // @ts-ignore
    dispatch({
      type: LocalDBActionType.LOGOUT,
      payload: undefined,
    });
  };

  const getBorrowers = ({ item }) => {
    const borrowersGroup = getBorrowersGroup(state.transactions);
    return Object.keys(borrowersGroup).reduce((acc, name) => {
      const borrowers = borrowersGroup[name];
      const borrowersByItem =
        item === undefined
          ? borrowers
          : borrowers.filter(
              (borrower) => borrower.item.toLowerCase() === item.toLowerCase()
            );
      const borrowersReturnedTransactionDates = borrowersByItem
        .filter((t) => t.type !== TransactionType.BORROWED)
        .map(({ type }) => TransactionType.getReturnedTransactionDate(type));
      const filteredBorrowersByItem = borrowersByItem.filter(
        (t) =>
          !borrowersReturnedTransactionDates.includes(t.transactionDate) &&
          t.type === TransactionType.BORROWED
      );
      return filteredBorrowersByItem.length === 0
        ? acc
        : {
            ...acc,
            [name]: filteredBorrowersByItem,
          };
    }, {});
  };

  const getAllBorrowers = () => {
    const allBorrowers = getBorrowers({ item: undefined });
    return Object.keys(allBorrowers).reduce((acc, name) => {
      return acc.concat({
        name,
        items: allBorrowers[name]
      });
    }, []);
  }

  const getTransaction = ({ transactionDate }) =>
    state.transactions.find((t) => t.transactionDate === transactionDate);

  const getInventoryFromTransactions = () => {
    const transactionsGroup = groupBy({
      list: state.transactions,
      fieldNames: ["item"],
    });

    return Object.keys(transactionsGroup).reduce((acc, item) => {
      const itemTransactions = transactionsGroup[item];
      const { quantity: total = 0 } =
        tools.find((tool) => tool.item.toLowerCase() === item.toLowerCase()) ||
        {};
      const borrowedTotal = sumOfBorrowedTool(itemTransactions);
      const returnedTotal = sumOfReturnedTool(itemTransactions);
      const available = total - (borrowedTotal - returnedTotal);

      const borrowersGroup = getBorrowersGroup(itemTransactions);
      const borrowers = Object.keys(borrowersGroup).reduce((acc, borrower) => {
        const borrowerTransactions = borrowersGroup[borrower];
        const borrowedTransactionsTotal =
          sumOfBorrowedTool(borrowerTransactions);
        const returnedTransactionsTotal =
          sumOfReturnedTool(borrowerTransactions);
        const borrowedTotal =
          borrowedTransactionsTotal - returnedTransactionsTotal;
        if (borrowedTotal === 0) {
          return acc;
        } else {
          return acc.concat(`${borrower} (${borrowedTotal})`);
        }
      }, []);

      const action = getPossibleTransactionAction({ total, available });

      /* console.log(
        `item ${item}, borrowedTotal ${borrowedTotal}, returnedTotal ${returnedTotal}, available ${available}, borrowersContent ${borrowers}, action (${action})`
      );*/
      return acc.concat({
        item,
        total,
        available,
        borrowers,
        action,
      });
    }, []);
  };

  const getInventory = () => {
    const inventoryWithTransactions = getInventoryFromTransactions();
    const inventoryWithNoTransaction = tools.filter(
      (zt) =>
        !inventoryWithTransactions.some(
          (t) => t.item.toLowerCase() === zt.item.toLowerCase()
        )
    );
    return inventoryWithTransactions.concat(
      inventoryWithNoTransaction.map((zt) => {
        return {
          item: zt.item,
          total: zt.quantity,
          available: zt.quantity,
          borrowers: [],
          action: getPossibleTransactionAction({
            total: zt.quantity,
            available: zt.quantity,
          }),
        };
      })
    );
  };

  const borrowTool = (data) => {
    const borrowTransaction = {
      ...data,
      ...getComputedTransactionData(),
      type: TransactionType.BORROWED,
    };

    console.log(`borrowTransaction`, borrowTransaction);
    // @ts-ignore
    dispatch({
      type: LocalDBActionType.ADD_TRANSACTION,
      payload: borrowTransaction,
    });
  };

  const returnTool = (data) => {
    const { transactionDate: borrowTransactionDate } = data;
    const returnTransaction = {
      ...data,
      ...getComputedTransactionData(),
      type: TransactionType.RETURNED(borrowTransactionDate),
    };

    console.log(`returnTransaction`, returnTransaction);
    // @ts-ignore
    dispatch({
      type: LocalDBActionType.ADD_TRANSACTION,
      payload: returnTransaction,
    });
  };

  return (
    <LocalDBContext.Provider
      // @ts-ignore
      value={{
        getCurrentUser,
        doesUserExists,
        login,
        logout,
        getInventory,
        getBorrowers,
        borrowTool,
        returnTool,
        getTransaction,
        getAllBorrowers,
      }}
    >
      {children}
    </LocalDBContext.Provider>
  );
};
