import { Formik } from "formik";
import React from "react";
import { Form, Button, FloatingLabel } from "react-bootstrap";
import { AVHeader } from "../component/AVHeader";
import { useLocalDB } from "../database/LocalDB";
import "../App.css";

export const Login = () => {
  const { doesUserExists, login } = useLocalDB();
  return (
    <>
      <AVHeader username={undefined} />
      <br className="mt-30" />
      <Formik
        initialValues={{ username: "", password: "" }}
        validateOnChange={false}
        validateOnBlur={false}
        validate={(values) => {
          return {
            ...(values.username.length === 0
              ? { username: "Username is required." }
              : {}),
            ...(values.password.length === 0
              ? { password: "Password is required." }
              : {}),
            ...(values.username.length > 0 &&
            values.password.length > 0 &&
            !doesUserExists({
              username: values.username,
              password: values.password,
            })
              ? { authentication: "Username not found." }
              : {}),
          };
        }}
        onSubmit={(values, actions) => {
          login(values.username);
        }}
      >
        {({ handleSubmit, handleChange, values, touched, isValid, errors }) => (
          <Form noValidate onSubmit={handleSubmit}>
            <Form.Group
              className="mb-3 w-75 container-center"
              controlId="formBasicUsername"
            >
              <FloatingLabel
                controlId="floatingInput"
                label="Username"
                className="mb-3"
              >
                <Form.Control
                  placeholder="Username"
                  type="text"
                  name="username"
                  value={values.username}
                  onChange={handleChange}
                  isInvalid={!!errors.username || !!errors.authentication}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.username || errors.authentication}
                </Form.Control.Feedback>
              </FloatingLabel>
            </Form.Group>
            <Form.Group
              className="mb-3 w-75 container-center"
              controlId="formBasicPassword"
            >
              <FloatingLabel controlId="floatingPassword" label="Password">
                <Form.Control
                  placeholder="Password"
                  type="password"
                  name="password"
                  value={values.password}
                  onChange={handleChange}
                  isInvalid={!!errors.password}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.password}
                </Form.Control.Feedback>
              </FloatingLabel>
            </Form.Group>
            <div className="w-75 container-center">
              <Button variant="primary" className="w-100" type="submit" size="lg">
                Login
              </Button>
            </div>
          </Form>
        )}
      </Formik>
    </>
  );
};
