import { capitalizeFirstLetter } from "./String";

export const groupBy = ({
  list = [],
  fieldNames = [],
  delimiter = " ",
}) => {
  return list.reduce((acc, current) => {
    const fieldValue = fieldNames
      .map((fieldName) => capitalizeFirstLetter(current[fieldName]))
      .join(delimiter);
    return {
      ...acc,
      [fieldValue]: (acc[fieldValue] || []).concat(current),
    };
  }, {});
};

export const aggregateSum = ({
  list = [],
  byFieldName = "",
  fieldNameToSum = ""
}) => {
  const byFieldNameGroup = groupBy({
    list,
    fieldNames: [byFieldName]
  });

  return Object.keys(byFieldNameGroup).reduce((acc, current) => {
    return acc.concat({
      byFieldName: current,
      sum: byFieldNameGroup[current].reduce((a, b) => a + b[fieldNameToSum], 0),
    });
  }, []);
}
