import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import React, { useState } from "react";
import { Button } from "react-bootstrap";
import { BorrowerForm } from "../component/BorrowerForm";
import { useLocalDB } from "../database/LocalDB";
import { TransactionAction } from "../util/UI";
import { ReturnForm } from "../component/ReturnForm";
import "../App.css";

const { SearchBar, ClearSearchButton } = Search;

export const Inventory = () => {
  const { getInventory, getBorrowers } = useLocalDB();

  const [borrow, setBorrow] = useState({
    show: false,
    item: "",
    available: 0,
  });

  const [returnTool, setReturnTool] = useState({
    header: "",
    show: false,
    item: "",
    borrowers: {},
    borrowerName: "",
    transactionDate: 0,
  });

  const columns = [
    {
      dataField: "item",
      text: "Item Name",
      searchable: true,
      sort: true,
    },
    {
      dataField: "total",
      text: "Total Stocks",
      searchable: false,
      sort: true,
      headerStyle: (colum, colIndex) => {
        return { width: "17%" };
      },
    },
    {
      dataField: "available",
      text: "Available Stocks",
      searchable: false,
      sort: true,
    },
    {
      dataField: "borrowers",
      text: "Borrowers (N tools)",
      formatter: (cell, row) => (
        <div>
          {row.borrowers.map((el, i) => (
            <div key={i}>{el}</div>
          ))}
        </div>
      ),
      searchable: false,
      headerStyle: (colum, colIndex) => {
        return { width: "23%" };
      },
    },
    {
      dataField: "action",
      text: "Action",
      formatter: (cell, row) => {
        return (
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
            }}
          >
            {" "}
            {(cell === TransactionAction.BORROW_TOOL ||
              cell === TransactionAction.BORROW_AND_RETURN_TOOL) && (
              <Button
                variant="outline-primary"
                size="sm"
                onClick={() =>
                  setBorrow({
                    show: true,
                    item: row.item,
                    available: row.available,
                  })
                }
              >
                Borrow Tool
              </Button>
            )}
            <div style={{ height: 10 }}></div>
            {(cell === TransactionAction.RETURN_TOOL ||
              cell === TransactionAction.BORROW_AND_RETURN_TOOL) && (
              <Button
                variant="outline-primary"
                size="sm"
                onClick={() => {
                  const borrowers = getBorrowers({ item: row.item });
                  const borrowerName = Object.keys(borrowers)[0];
                  setReturnTool({
                    header: `Return ${row.item}`,
                    show: true,
                    item: row.item,
                    borrowers,
                    borrowerName,
                    transactionDate: borrowers[borrowerName][0].transactionDate,
                  });
                }}
              >
                Return Tool
              </Button>
            )}
          </div>
        );
      },
      searchable: false,
      headerStyle: (colum, colIndex) => {
        return { width: "20%" };
      },
    },
  ];

  return (
    <>
      <BorrowerForm
        state={borrow}
        handleClose={() =>
          setBorrow({
            show: false,
            item: "",
            available: 0,
          })
        }
      />
      <ReturnForm
        state={returnTool}
        handleClose={() =>
          setReturnTool({
            headers: "",
            show: false,
            item: "",
            borrowers: {},
            borrowerName: "",
            transactionDate: 0,
          })
        }
      />
      <ToolkitProvider
        bootstrap4
        keyField="item"
        data={getInventory()}
        columns={columns}
        search={{ defaultSearch: "" }}
      >
        {(props) => (
          <div className="w-75 container-center">
            <br className="mt-30" />
            <SearchBar
              {...props.searchProps}
              srText="Search by Tool name"
              placeholder="Tool name"
            />
            <ClearSearchButton {...props.searchProps} />
            <br className="mt-30" />
            <br className="mt-30" />
            <BootstrapTable
              {...props.baseProps}
              noDataIndication="No Inventory"
              hover
              condensed
              striped
              bordered={ false }
            />
          </div>
        )}
      </ToolkitProvider>
    </>
  );
};
